<template>
  <!-- <div class="fs-2 text-center fw-bold mb-3">{{ showData }}</div> -->
  <div style="height: calc(80vh); overflow-y: scroll">
    <table class="tw-table align-middle">
      <tr>
        <th
          v-for="(i, index) in this.data.title"
          :key="index"
          :style="redData.indexOf(i) !== -1 ? 'background-color:#ff0000;' : ''"
        >
          {{ i }}
        </th>
      </tr>
      <tr v-for="(i, index) in 20" :key="index">
        <td v-for="(j, index) in this.data.title">td</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
    props: {
        showData: {
            type: String,
            default: '全部'
        }
    },
    data() {
        return {
            data: {
                title:[],
                content:[],
            },
            redData: ['活耀度','賣場活耀度','整體活耀度']
        }
    },
    created() {
        this.inputData()
    },  
    watch: {
        showData: { 
            handler(newVlue,Oldvlue) {
                this.inputData()
            },
            immediate: true
        }
    },
    methods: {
        inputData() {
            let status = this.showData.split('_')[0]
            let title = []
            let content = []
            if (this.showData.split('_')[0] === '全部') {
                title = ['使用者ID','姓名','帳號','關聯','加入賣場數','活耀度','註冊時間','身分別','擁有賣場數','方案','訂閱到期日']
            }
            else if (this.showData.split('_')[0] === '賣家') {
                title = ['使用者ID','姓名','帳號','方案','賣場ID','賣場名稱','開立時間','商品數','FB文章數','Line發布數','顧客數','訂單數','帳單數','賣場活耀度','整體活耀度']
            }
            else if (this.showData.split('_')[0] === '顧客') {
                title = ['使用者ID','姓名','帳號','關聯','加入賣場數','下單數','結單數','註冊時間','活耀度']
            }
            this.data.title = title
            console.log(this.redData.indexOf('1賣場活耀度'))
        }
    }

}
</script>