                    "
                    @mousemove="mouseEvent('mousemove', '1-2')"
                    @mouseout="mouseEvent('mouseout', '1-2')"
                    @click="mouseEvent('click', '1-2')"
                  />
                </span>
                <ul>
                  <li>
                    <span class="tf-nc" style="border: 0px">
                      <input
                        type="submit"
                        class="btn"
                        value="試用中 &#10;(100人)"
                        :class="
                          mouseEventTryCSS['1-2-1']
                            ? mouseEventTryCSS['1-2-1']
                            : 'btn-outline-success'
                        "
                        @mousemove="mouseEvent('mousemove', '1-2-1')"
                        @mouseout="mouseEvent('mouseout', '1-2-1')"
                        @click="mouseEvent('click', '1-2-1')"
                      />
                    </span>
                  </li>
                  <li>
                    <span class="tf-nc" style="border: 0px">
                      <input
                        type="submit"
                        class="btn"
                        value="試用完沒下落 &#10;(100人)"
                        :class="
                          mouseEventTryCSS['1-2-2']
                            ? mouseEventTryCSS['1-2-2']
                            : 'btn-outline-success'
                        "
                        @mousemove="mouseEvent('mousemove', '1-2-2')"
                        @mouseout="mouseEvent('mouseout', '1-2-2')"
                        @click="mouseEvent('click', '1-2-2')"
                      />
                    </span>
                  </li>
                  <li>
                    <span class="tf-nc" style="border: 0px">
                      <input
                        type="submit"
                        class="btn"
                        value="沒試用 &#10;(100人)"
                        :class="
                          mouseEventTryCSS['1-2-3']
                            ? mouseEventTryCSS['1-2-3']
                            : 'btn-outline-success'
                        "
                        @mousemove="mouseEvent('mousemove', '1-2-3')"
                        @mouseout="mouseEvent('mouseout', '1-2-3')"
                        @click="mouseEvent('click', '1-2-3')"
                      />
                    </span>
                    <ul>
                      <li>
                        <span class="tf-nc" style="border: 0px">
                          <input
                            type="submit"
                            class="btn"
                            value="開通後沒試用 &#10;(100人)"
                            :class="
                              mouseEventTryCSS['1-2-3-1']
                                ? mouseEventTryCSS['1-2-3-1']
                                : 'btn-outline-success'
                            "
                            @mousemove="mouseEvent('mousemove', '1-2-3-1')"
                            @mouseout="mouseEvent('mouseout', '1-2-3-1')"
                            @click="mouseEvent('click', '1-2-3-1')"
                          />
                        </span>
                      </li>
                      <li>
                        <span class="tf-nc" style="border: 0px">
                          <input
                            type="submit"
                            class="btn"
                            value="建立賣場後 &#10; 沒連繫客服開通 &#10; (100人)"
                            :class="
                              mouseEventTryCSS['1-2-3-2']
                                ? mouseEventTryCSS['1-2-3-2']
                                : 'btn-outline-success'
                            "
                            @mousemove="mouseEvent('mousemove', '1-2-3-2')"
                            @mouseout="mouseEvent('mouseout', '1-2-3-2')"
                            @click="mouseEvent('click', '1-2-3-2')"
                          />
                        </span>
                      </li>
                    </ul>
                  </li>
                </ul>
              </li>
            </ul>
          </li>
          <li>
            <span class="tf-nc" style="border: 0px">
              <input
                type="submit"
                class="btn"
                value="顧客 &#10;(100人)"
                :class="
                  mouseEventTryCSS['2']
                    ? mouseEventTryCSS['2']
                    : 'btn-outline-success'
                "
                @mousemove="mouseEvent('mousemove', '2')"
                @mouseout="mouseEvent('mouseout', '2')"
                @click="mouseEvent('click', '2')"
              />
            </span>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
    data() {
        return {
            mouseRecord: [],
            mouseEventTryCSS:{}
        }
    },
    methods: {
        mouseEvent(status, location) {
            location = location.split('-')
            let temp = []
            location.forEach((item,index) => {
                let data = ''
                if (index !== 0) data = `${temp[index-1]}-${item}`
                else data = item 
                temp.push(data)
            });
            if (status === 'mousemove') {
                this.mouseEventTryCSS = {}
                temp.forEach((item) => {
                    this.mouseEventTryCSS[item] = 'btn-success'
                })
            }
            if (status === 'mouseout') {
                this.mouseEventTryCSS = {}
            }
            if (status === 'click') {
                this.mouseRecord = temp            
                console.log(temp[0])
                if(temp[0] === '0') this.mouseEventTryCSS = {}
                this.$emit('chooseIdentity', this.mouseRecord[temp.length-1])
            }
            this.mouseRecord.forEach((item) => {
                this.mouseEventTryCSS[item] = 'btn-secondary'
            })
        }
    }
}
</script>
