<template>
  <div class="tw-bg-main p-5" style="height: 100vh">
    <div class="tw-container">
      <div>
        <!-- 上方按鈕 -->
        <div class="d-flex justify-content-between mb-2">
          <div>
            <button
              type="button"
              class="btn me-2"
              :class="
                showcontain === 'table' ? 'btn-success' : 'btn-outline-success'
              "
              @click="showcontain = 'table'"
            >
              <i class="bi bi-table"></i>
            </button>
            <button
              type="button"
              class="btn me-2"
              :class="
                showcontain === 'chart' ? 'btn-success' : 'btn-outline-success'
              "
              @click="showcontain = 'chart'"
            >
              <i class="bi bi-clipboard-data"></i>
            </button>
          </div>
          <div class="fs-2 text-center fw-bold mt-3">{{ showData }}</div>
          <div>
            <button
              type="button"
              class="btn btn-outline-success"
              @click="showModal('identity')"
            >
              <i class="bi bi-diagram-3"></i>
            </button>
          </div>
        </div>
        <!-- 內容 -->
        <div class="p-3">
          <template v-if="showcontain === 'table'">
            <Table :showData="showData"></Table>
          </template>
          <template v-else-if="showcontain === 'chart'">
            <Chart :showData="showData"> </Chart>
          </template>
        </div>
      </div>
    </div>
  </div>
  <!-- 選擇身分 modal -->
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
    ref="identityModal"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title tw-border-start fw-bold">選擇身分</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <!-- 樹狀圖 -->
          <IdentityTry @chooseIdentity="chooseIdentity"></IdentityTry>
        </div>
      </div>
    </div>
  </div>
  <!-- 空的 modal -->
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
    ref=""
  >
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title tw-border-start fw-bold">Title</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">...</div>
        <div class="modal-footer">
          <button
            type="button"
            class="tw-btn tw-btn-secondary"
            data-bs-dismiss="modal"
          >
            關閉
          </button>
          <button type="button" class="tw-btn tw-btn-success">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IdentityTry from '../components/indentityTry.vue'
import Table from '../components/table.vue'
import Chart from '../components/chart.vue'

import RegistrationInformation from '../data/RegistrationInformation.json'
import SubscribeInformation from '../data/SubscribeInformation.json'
import UseInformation from '../data/UseInformation.json'

export default{
  components: {IdentityTry, Table, Chart},
  data(){
    return{
      // modal
      identityModal: {},
      // data
      showcontain:'table',
      showData: '全部',

    }
  },
  created() {
    this.dataProcessing(true,'')
  },
  mounted() {
    this.createModals([
      'identityModal'
    ])
  },
  methods: {
    showModal(status, item) {
      if (status === 'identity') {
        console.log('開啟模組')
        this.identityModal.show()
      }
    },
    chooseIdentity(value) {
      this.identityModal.hide()
      if(value==='0') this.showData = '全部'
      if(value==='1') this.showData = '賣家'
      else if(value==='1-1') this.showData = '賣家_已付款'
      else if(value==='1-1-1') this.showData = '賣家_已付款_年費'
      else if(value==='1-1-2') this.showData = '賣家_已付款_季費'
      else if(value==='1-2') this.showData = '賣家_未付款'
      else if(value==='1-2-1') this.showData = '賣家_未付款_試用中'
      else if(value==='1-2-2') this.showData = '賣家_未付款_試用完沒下落'
      else if(value==='1-2-3-1') this.showData = '賣家_未付款_沒試用_開通後沒試用'
      else if(value==='1-2-3-2') this.showData = '賣家_未付款_沒試用_建立後沒連繫客服開通'
      else if(value==='2') this.showData = '顧客'
    },
    // 資料導入/處理
    dataProcessing(create, status) {
      if (create) this.dataCombin()
      this.dataFilter()
    },
    // 資料導入與整合
    dataCombin() {
      console.log(RegistrationInformation)
    },
    // 資料過濾
    dataFilter() {

    }

  },

}

</script>