<template>
  <div class="row p-3" style="height: calc(80vh); overflow-y: scroll">
    <div class="col-3 border p-3">
      <template v-for="(i, index) in this.data.title" :key="index">
        <div class="form-check mt-2">
          <input
            class="form-check-input"
            type="radio"
            name="flexRadioDefault"
            :id="i"
            @click="showChart(i)"
          />
          <label class="form-check-label" :for="i">
            {{ i }}
          </label>
        </div>
      </template>
    </div>
    <div class="col-9 border p-3">
      <div>{{ this.chartShow.title }}</div>

      <div id="container">
        <canvas id="canvas"></canvas>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    props: {
        showData: {
            type: String,
            default: '全部'
        }
    },
    data() {
        return {
            data: {
                title: [],
                data: [],
            },
            chartShow:{
                title: '',
            },
        }
    },
    created() {
        
    },  
    watch: {
        showData: { 
            handler(newVlue,Oldvlue) {
                this.inputData()
                // this.clearCanvas()
                // this.showChart('0')
            },
            immediate: true
        }
    },
    methods: {
        inputData() {
            let status = this.showData.split('_')[0]
            let title = []
            let content = []
            if (this.showData.split('_')[0] === '全部') {
                title = ['申請試用趨勢圖', '訂閱趨勢圖', '帳號關連分布圖']
            }
            else if (this.showData.split('_')[0] === '賣家') {
                title = ['賣家_分析圖表1', '賣家_分析圖表2', '賣家_分析圖表3', '賣家_分析圖表4']
            }
            else if (this.showData.split('_')[0] === '顧客') {
                title = ['顧客_分析圖表1', '顧客_分析圖表2', '顧客_分析圖表3', '顧客_分析圖表4']
            }
            this.data.title = title
        },
        clearCanvas(){
            $('#canvas').remove();
            $('#container').append('<canvas id="canvas"></canvas>');
        },
        showChart(show) {
            this.chartShow.title = show
            this.clearCanvas()
            if (show === '申請試用趨勢圖') { this.createChartline()}
            else if (show === '訂閱趨勢圖') { this.creatChartBar()}
            else if (show === '帳號關連分布圖') {this.createChartPie()}
            else {this.createChartline()}
        },
        createChartline() {
            
            console.log('生成')
            let ChartData = {
                labels: ["3", "6", "9", "12", "15", "18", "21", "24", "27"], //顯示區間名稱
                datasets: [{
                    label: '訂閱', // tootip 出現的名稱
                    lineTension: 0, // 曲線的彎度，設0 表示直線
                    backgroundColor: "#ea464d",
                    borderColor: "#ea464d",
                    borderWidth: 5,
                    data: [10, 12, 15, 18, 22, 33, 50, 60, 130], // 資料
                    fill: false, // 是否填滿色彩
                }, {
                    label: '試用',
                    lineTension: 0,
                    fill: false,
                    backgroundColor: "#29b288",
                    borderColor: "#29b288",
                    borderWidth: 5,
                    data: [12, 14, 18, 20, 21, 34, 60, 80, 200],
                },]
            };
            var ctx = document.getElementById("canvas").getContext("2d");
            this.drawLineCanvas(ctx,ChartData);
        },
        drawLineCanvas(ctx,data) {
            var config = {  //先建立一個 chart
                type: 'line', // 型態
                data: data,
                options: {
                        responsive: true,
                        legend: { //是否要顯示圖示
                            display: true,
                        },
                        tooltips: { //是否要顯示 tooltip
                            enabled: true
                        },
                        scales: {  //是否要顯示 x、y 軸
                            xAxes: [{
                                display: true
                            }],
                            yAxes: [{
                                display: true
                            }]
                        },
                    }
            }
            var myChart =new Chart(ctx, config);
        },
        createChartPie() {
            $('#canvas').remove();
            $('#container').append('<canvas id="canvas"></canvas>');
            console.log('ssss')
            const ctx = document.getElementById('canvas');
            let data = {
            labels: [
                'Red',
                'Blue',
                'Yellow'
            ],
            datasets: [{
                label: '饼图实例',
                data: [300, 50, 100],
                backgroundColor: [
                'rgb(255, 99, 132)',
                'rgb(54, 162, 235)',
                'rgb(255, 205, 86)'
                ],
                hoverOffset: 4
            }]
            };
            var config = {
            type: 'pie',
            data: data,
            options: {
                responsive: true, // 设置图表为响应式，根据屏幕窗口变化而变化
                maintainAspectRatio: false,// 保持图表原有比例
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero:true
                        }
                    }]
                }
            }
            };
            var myChart =new Chart(ctx, config);
        },
        creatChartBar() {
            const ctx = document.getElementById('canvas');
            let data = {
                labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
                datasets: [{
                    label: '# of Votes',
                    data: [12, 19, 3, 5, 2, 3],
                    borderWidth: 1
                }]
            }
            var config = {
                type:'bar',
                data: data,
                options: {
                    scales: {
                        y: {
                            beginAtZero: true
                        }
                    }
                }

            }
            var myChart =new Chart(ctx, config);
        }
    }

    
}
</script>